body {
  margin: 0;
  background: pink;
}

.homepage {
  display: flex;
}

.nav-bar {
  flex-basis: 250px;
  height: 100vh;
  padding: 0 40px;
  background: white
}

.content { 
  padding: 40px;
  background: rgb(231, 231, 231);
  overflow: auto;
  height: 100vh;
  flex: 1;
}

.feed {
  display: flex;
  flex-wrap: wrap;
}

.square { 
  width: 200px;
  height: 200px;
  background-color: grey;
  margin: 30px;
}